import c_gp from '../imgs/games/uno/gp.png';
import c_gr from '../imgs/games/uno/gr.png';
import c_gs from '../imgs/games/uno/gs.png';
import c_r0 from '../imgs/games/uno/r0.png';
import c_r1 from '../imgs/games/uno/r1.png';
import c_r2 from '../imgs/games/uno/r2.png';
import c_r3 from '../imgs/games/uno/r3.png';
import c_r4 from '../imgs/games/uno/r4.png';
import c_r5 from '../imgs/games/uno/r5.png';
import c_r6 from '../imgs/games/uno/r6.png';
import c_r7 from '../imgs/games/uno/r7.png';
import c_r8 from '../imgs/games/uno/r8.png';
import c_r9 from '../imgs/games/uno/r9.png';
import c_rp from '../imgs/games/uno/rp.png';
import c_rr from '../imgs/games/uno/rr.png';
import c_rs from '../imgs/games/uno/rs.png';
import c_y0 from '../imgs/games/uno/y0.png';
import c_y1 from '../imgs/games/uno/y1.png';
import c_y2 from '../imgs/games/uno/y2.png';
import c_y3 from '../imgs/games/uno/y3.png';
import c_y4 from '../imgs/games/uno/y4.png';
import c_y5 from '../imgs/games/uno/y5.png';
import c_y6 from '../imgs/games/uno/y6.png';
import c_y7 from '../imgs/games/uno/y7.png';
import c_y8 from '../imgs/games/uno/y8.png';
import c_y9 from '../imgs/games/uno/y9.png';
import c_yp from '../imgs/games/uno/yp.png';
import c_yr from '../imgs/games/uno/yr.png';
import c_ys from '../imgs/games/uno/ys.png';

import c_bc from '../imgs/games/uno/bc.png';
import c_gc from '../imgs/games/uno/gc.png';
import c_rc from '../imgs/games/uno/rc.png';
import c_yc from '../imgs/games/uno/yc.png';

import c_bf from '../imgs/games/uno/bf.png';
import c_gf from '../imgs/games/uno/gf.png';
import c_rf from '../imgs/games/uno/rf.png';
import c_yf from '../imgs/games/uno/yf.png';

import backCard from '../imgs/games/uno/card.png';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowUp } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";

import socket from './mainSocket.js';

import './css/Uno.css';

function Uno(props) {
    const [joinMode, setJoinMode] = useState(false);
    const [gameState, setGameState] = useState('');
    const [sessionNum, setSessionNum] = useState('');
    const [errorMsg, setErrorMsg] = useState('');
    const [host, setHost] = useState(false);
    
    const [playerNum, setPlayerNum] = useState('');
    const [topPlayerCards, setTopPlayerCards] = useState(0);
    const [leftPlayerCards, setLeftPlayerCards] = useState(0);
    const [rightPlayerCards, setRightPlayerCards] = useState(0);
    const [deckCard, setDeckCard] = useState('');
    const [changeColor, setChangeColor] = useState(false);
    const [plusFour, setPlusFour] = useState(false);
    
    const [gameArrow, setGameArrow] = useState('down');
    const [gamePlayers, setGamePlayers] = useState('');
    const [gameCards, setGameCards] = useState([]);

    useEffect(() => {
        axios.get('https://sappgames.herokuapp.com/').catch(() => {}).finally(() => {});
        console.log('ah..');
    }, []);

    const cardStrToPath = card => {
        console.log('card src is:' + card);
            if('b0' == card) {
                return c_b0;}
            else if('b1' == card) {
                return c_b1;}
            else if('b2' == card) {
                return c_b2;}
            else if('b3' == card) {
                return c_b3;}
            else if('b4' == card) {
                console.log('went to b4');
                return c_b4;}
            else if('b5' == card) {
                return c_b5;}
            else if('b6' == card) {
                return c_b6;}
            else if('b7' == card) {
                return c_b7;}
            else if('b8' == card) {
                return c_b8;}
            else if('b9' == card) {
                return c_b9;}
            else if('bp' == card) {
                return c_bp;}
            else if('br' == card) {
                return c_br;}
            else if('bs' == card) {
                return c_bs;}
            else if('c' == card) {
                return c_c;}
            else if('f' == card) {
                return c_f;}
            else if('g0' == card) {
                return c_g0;}
            else if('g1' == card) {
                return c_g1;}
            else if('g2' == card) {
                return c_g2;}
            else if('g3' == card) {
                return c_g3;}
            else if('g4' == card) {
                return c_g4;}
            else if('g5' == card) {
                return c_g5;}
            else if('g6' == card) {
                return c_g6;}
            else if('g7' == card) {
                return c_g7;}
            else if('g8' == card) {
                return c_g8;}
            else if('g9' == card) {
                return c_g9;}
            else if('gp' == card) {
                return c_gp;}
            else if('gr' == card) {
                return c_gr;}
            else if('gs' == card) {
                return c_gs;}
            else if('r0' == card) {
                return c_r0;}
            else if('r1' == card) {
                return c_r1;}
            else if('r2' == card) {
                return c_r2;}
            else if('r3' == card) {
                return c_r3;}
            else if('r4' == card) {
                return c_r4;}
            else if('r5' == card) {
                return c_r5;}
            else if('r6' == card) {
                return c_r6;}
            else if('r7' == card) {
                return c_r7;}
            else if('r8' == card) {
                return c_r8;}
            else if('r9' == card) {
                return c_r9;}
            else if('rp' == card) {
                return c_rp;}
            else if('rr' == card) {
                return c_rr;}
            else if('rs' == card) {
                return c_rs;}
            else if('y0' == card) {
                return c_y0;}
            else if('y1' == card) {
                return c_y1;}
            else if('y2' == card) {
                return c_y2;}
            else if('y3' == card) {
                return c_y3;}
            else if('y4' == card) {
                return c_y4;}
            else if('y5' == card) {
                return c_y5;}
            else if('y6' == card) {
                return c_y6;}
            else if('y7' == card) {
                return c_y7;}
            else if('y8' == card) {
                return c_y8;}
            else if('y9' == card) {
                return c_y9;}
            else if('yp' == card) {
                return c_yp;}
            else if('yr' == card) {
                return c_yr;}
            else if('ys' == card) {
                return c_ys;}
            else if('bc' == card) {
                return c_bc;}
            else if('gc' == card) {
                return c_gc;}
            else if('rc' == card) {
                return c_rc;}
            else if('yc' == card) {
                return c_yc;}
            else if('bf' == card) {
                return c_bf;}
            else if('gf' == card) {
                return c_gf;}
            else if('rf' == card) {
                return c_rf;}
            else if('yf' == card) {
                return c_yf;}
    }

    const getArrowDir = () => {
        switch(gameArrow) {
            case 'down':
                return 180;
            case 'left':
                return 270;
            case 'up':
                return 0;
            case 'right':
                return 90;
        }
    }

    const settingOutside = num => {
        console.log('wanted: ' + num);
        setPlayerNum([num]);
        setTimeout(() => console.log('num is: ' + playerNum), [1000]);
    }

    const joinCreateSession = create => {
        if(sessionNum.length > 0 && sessionNum) {
            if(create) {
                socket.emit('uno-create', {sessionNum, username: props.username});
                socket.on('uno-invalid-create', response => {
                    setErrorMsg(response);
                    document.getElementById('createInp').style.borderColor = 'red';
                });
                socket.on('uno-create-success', response => {
                    setGamePlayers(getAllPlayers(response));
                    setHost(true);
                    settingOutside(0);
                    setGameState('init');
                    setJoinMode('game');
                });
                socket.on('uno-players-update', response => {
                    setGamePlayers(getAllPlayers(response));
                });
                socket.on('uno-players-start', () => {
                    console.log('player num is: ' + playerNum);
                    socket.emit('uno-get-cards', {sessionNum, username: props.username, playerNum});
                    socket.on('uno-send-cards', response => {
                        setGameCards(response.ownCards);
                        console.log(response.turnDir);
                        setGameArrow(response.turnDir);
                        setDeckCard(response.deckCard);
                        setTopPlayerCards(response.topCards);
                        if(response.gamePlayersLength > 2) {
                            setLeftPlayerCards(response.leftCards);
                        }
                        if(response.gamePlayersLength > 3) {
                            setLeftPlayerCards(response.leftCards);
                            setRightPlayerCards(response.rightCards);
                        }
                        setGameState('game');
                    });
                    socket.on('uno-players-move', () => {
                        socket.emit('uno-get-cards', {sessionNum, username: props.username, playerNum});
                        socket.on('uno-send-cards', response => {
                            setGameCards(response.ownCards);
                            setGameArrow(response.turnDir);
                            setDeckCard(response.deckCard);
                            setTopPlayerCards(response.topCards);
                            if(response.gamePlayersLength > 2) {
                                setLeftPlayerCards(response.leftCards);
                            }
                            if(response.gamePlayersLength > 3) {
                                setLeftPlayerCards(response.leftCards);
                                setRightPlayerCards(response.rightCards);
                            }
                            setGameState('game');
                        });
                    });
                });
            } else {
                socket.emit('uno-join', {sessionNum, username: props.username});
                socket.on('uno-invalid-join', response => {
                    setErrorMsg(response);
                    document.getElementById('joinInp').style.borderColor = 'red';
                });
                socket.on('uno-join-success', response => {
                    setGamePlayers(getAllPlayers(response.server));
                    settingOutside(response.playerNum);
                    console.log('the nu miss: ' + playerNum);
                    setGameState('init');
                    setJoinMode('game');
                });
                socket.on('uno-players-update', response => {
                    setGamePlayers(getAllPlayers(response));
                });
                socket.on('uno-players-start', () => {
                    console.log('player num is: ' + playerNum);
                    socket.emit('uno-get-cards', {sessionNum, username: props.username, playerNum});
                    socket.on('uno-send-cards', response => {
                        setGameCards(response.ownCards);
                        console.log(response.turnDir);
                        setGameArrow(response.turnDir);
                        setDeckCard(response.deckCard);
                        setTopPlayerCards(response.topCards);
                        if(response.gamePlayersLength > 2) {
                            setLeftPlayerCards(response.leftCards);
                        }
                        if(response.gamePlayersLength > 3) {
                            setLeftPlayerCards(response.leftCards);
                            setRightPlayerCards(response.rightCards);
                        }
                        setGameState('game');
                    });
                    socket.on('uno-players-move', () => {
                        socket.emit('uno-get-cards', {sessionNum, username: props.username, playerNum});
                        socket.on('uno-send-cards', response => {
                            setGameCards(response.ownCards);
                            setGameArrow(response.turnDir);
                            setDeckCard(response.deckCard);
                            setTopPlayerCards(response.topCards);
                            if(response.gamePlayersLength > 2) {
                                setLeftPlayerCards(response.leftCards);
                            }
                            if(response.gamePlayersLength > 3) {
                                setLeftPlayerCards(response.leftCards);
                                setRightPlayerCards(response.rightCards);
                            }
                            setGameState('game');
                        });
                    });
                });
            }
        } else {
            if(create) {
                document.getElementById('createInp').style.borderColor = 'red';
                setErrorMsg('Invalid session code!');
            } else {
                document.getElementById('joinInp').style.borderColor = 'red';
                setErrorMsg('Invalid session code!');
            }
        }
    }

    const getAllPlayers = session => {
        let temp = [];
        for(let player of session.players) {
            temp.push(player.name);
        }
        console.log('set all players to ' + temp.length);
        return temp;
    }

    const printInitPlayers = players => {
        let temp = [];
        let index = 0;
        for(const player of players) {
            temp.push(<div key={v4()} className="my-2">{`Player ${++index}: ${player}`}</div>);
        }
        return temp;
    }

    const startGame = () => {
        socket.emit('uno-start', {sessionNum, username: props.username});
        console.log(`sessionNum is: ${sessionNum}`);
    }

    const printCards = () => {
        const temp = [];
        for(const card of gameCards) {
            console.log('card drawn: ' + gameCards);
            temp.push(<img type="button" style={{width: '8rem'}} className="container uno-card-scale" src={cardStrToPath(card)} onClick={() => playCard(card)}/>);
        }
        return temp;
    }

    const playCard = card => {
        if(gameArrow == 'down' && !changeColor && !plusFour) {
            if(card == 'c') {
                setChangeColor(true);
            } else if(card == 'f') {
                setPlusFour(true);
            } else {
                socket.emit('uno-play-card', {sessionNum, username: props.username, card, playerNum});
            }
        }
    }

    const selectColor = card => {
        setChangeColor(false);
        setPlusFour(false);
        socket.emit('uno-play-card', {sessionNum, username: props.username, card, playerNum});
    }

    const drawCard = () => {
        if(gameArrow == 'down') {
            socket.emit('uno-draw-card', {sessionNum, username: props.username, playerNum});
        }
    }

    const printBackCards = (cardsAmount, rotate=0) => {
        const temp = [];
        for(let x=0;x<cardsAmount;x++) {
            temp.push(<img key={v4()} style={{width: '8rem', transform: `rotate(${rotate}deg)`}} className="container" src={backCard}/>);
        }
        return temp;
    }

    return (
        <div className="mt-3">
            <div className="d-flex justify-content-around">
                {!joinMode ?
                [
                    <button className="btn text-white p-5" style={{backgroundColor: 'green'}} onClick={() => [setErrorMsg(''), setSessionNum(''), setJoinMode('create')]}>Create Session</button>,
                    <button className="btn text-white p-5" style={{backgroundColor: 'green'}} onClick={() => [setErrorMsg(''), setSessionNum(''), setJoinMode('join')]}>Join Session</button>
                ]
                :
                joinMode == 'join' ?
                <div>
                    <div>
                        <button style={{height: '3rem'}} className="btn btn-danger" onClick={() => setJoinMode(false)}>Back</button>
                        <input id="joinInp" style={{height: '3rem'}} className="mx-4" onChange={e => setSessionNum(e.target.value)} placeholder="Session Code"/>
                        <button style={{height: '3rem'}} className="btn btn-primary" onClick={() => joinCreateSession(false)}>Join</button>
                    </div>
                    <div className="text-danger">{errorMsg}</div>
                </div>
                :
                joinMode == 'game' ?
                <div className="w-100" style={{minHeight: '100%', height: '79.4vh'}}>
                    {gameState == 'init' ?
                    <div>
                        {host ?
                        <button className="btn btn-primary" onClick={() => startGame()}>Start game</button>
                        :
                        ''
                        }
                        {printInitPlayers(gamePlayers)}
                    </div>
                    :
                    gameState == 'game' && deckCard ?
                    <div className="h-100 bg-primary d-flex">
                        <div className="container-fluid row">
                            {gamePlayers.length > 2 ?
                            <div className="d-flex col-1 position-absolute" style={{left: 10}}>
                                <div className="" style={{}}>
                                    {printBackCards(leftPlayerCards, 90)} 
                                </div>
                            </div>
                            :
                            ''}
                            <div className="align-self-start container col-12">
                                <div className="text-center">
                                    {printBackCards(topPlayerCards)}
                                </div>
                            </div>
                            <div className="align-self-center container col-12">
                                <div className="justify-center-between">
                                    {deckCard != '' ? <img style={{width: '8rem', marginRight: '10rem'}} className="container" src={cardStrToPath(deckCard)}/> : ''}
                                    {changeColor ?
                                    <div className="container-fluid" style={{position: 'absolute', zIndex: 2, bottom: '25%'}}>
                                        <img type="button" style={{width: '10rem'}} src={c_bc} onClick={() => selectColor('bc')}/>
                                        <img type="button" style={{width: '10rem'}} src={c_gc} onClick={() => selectColor('gc')}/>
                                        <img type="button" style={{width: '10rem'}} src={c_rc} onClick={() => selectColor('rc')}/>
                                        <img type="button" style={{width: '10rem'}} src={c_yc} onClick={() => selectColor('yc')}/>
                                    </div>
                                    :
                                    plusFour ?
                                    <div className="container-fluid" style={{position: 'absolute', zIndex: 2, bottom: '25%'}}>
                                        <img type="button" style={{width: '10rem'}} src={c_bf} onClick={() => selectColor('bf')}/>
                                        <img type="button" style={{width: '10rem'}} src={c_gf} onClick={() => selectColor('gf')}/>
                                        <img type="button" style={{width: '10rem'}} src={c_rf} onClick={() => selectColor('rf')}/>
                                        <img type="button" style={{width: '10rem'}} src={c_yf} onClick={() => selectColor('yf')}/>
                                    </div>
                                    :
                                    <FontAwesomeIcon id="custom-arrow" className={`${gameArrow == 'down' ? 'custom-arrow-turn' : 'custom-arrow'}`} icon={faArrowUp} size={'4x'} style={{transform: `rotate(${getArrowDir()}deg)`}}/>}
                                    <img type='button' className="uno-card-scale" style={{width: '8rem', marginLeft: '10rem'}} src={backCard} onClick={() => drawCard()}/>
                                </div>
                            </div>
                            <div className="align-self-end container col-12">
                                <div className="text-center">
                                    {printCards()}
                                </div>
                            </div>
                            {gamePlayers.length > 3 ?
                            <div className="d-flex col-1 position-absolute" style={{right: 10}}>
                                <div className="">
                                    {printBackCards(rightPlayerCards, 90)}
                                </div>
                            </div>
                            :
                            ''}
                        </div>
                    </div>
                    :
                    ''}
                </div>
                :
                <div>
                    <div>
                        <button style={{height: '3rem'}} className="btn btn-danger" onClick={() => setJoinMode(false)}>Back</button>
                        <input id="createInp" style={{height: '3rem'}} className="mx-4" onChange={e => setSessionNum(e.target.value)} placeholder="Session Code"/>
                        <button style={{height: '3rem'}} className="btn btn-primary" onClick={() => joinCreateSession(true)}>Create</button>
                    </div>
                    <div className="text-danger">{errorMsg}</div>
                </div>
                }
                
            </div>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Uno);