// Games with optional gambling:
// - Takki
// - bullshit.wtf
// - rock, paper, scissors
// - Tic-Tac-Toe
// - Black Jack

import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useEffect, useState } from "react";
import { connect } from "react-redux";
import langJson from '../../json/language.json';
import {setFeature} from '../../redux/';

import unoImage from '../../imgs/games/uno/f.png';
import Uno from "../../components/Uno";

function GamesPage(props) {
    const [topImagesSize, setTopImagesSize] = useState('7x');
    const [currentGame, setCurrentGame] = useState('');

    useEffect(() => {
        if(props.windowSize >= 992) {
            setTopImagesSize('7x');
        } else {
            setTopImagesSize('5x');
        }
    }, [props.windowSize]);
    return (
        <div>
            <div className="container-fluid" style={{backgroundColor: '#008000', border: '2px solid black', borderStyle: 'solid none solid none'}}>
                <button className="btn" onClick={() => !currentGame ? props.setFeature('') : setCurrentGame('')}><FontAwesomeIcon className="recipeFeatureBack" size={topImagesSize} icon={faTimes}/></button>
            </div>
            {!currentGame ?
            <div className="mt-3">
                <div className="card user-select-none" type="button" style={{width: '18rem', margin: 'auto', border: '3px black solid', borderRadius: '50px 50px 55px 55px'}}>
                    <img src={unoImage} onClick={() => [setCurrentGame('uno')]}/>
                </div>
            </div>
            :
            currentGame == 'uno' ?
            <Uno/>
            :
            ''}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        username: state.connection.username,
        ready: state.connection.ready,
        lang: state.general.lang,
        userData: state.connection.userData
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setFeature: val => dispatch(setFeature(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(GamesPage);